import { Component } from '@angular/core';

import { RadioPage } from '../radio/radio';
import { ContentPage } from '../content/content';
import { SchedulePage } from '../schedule/schedule';
import { VideoPage } from '../video/video';

import { Http } from '@angular/http';
import { parseString } from 'xml2js';
import { RestApiProvider } from '../../providers/rest-api/rest-api';
import 'rxjs/add/operator/map';

@Component({
  templateUrl: 'tabs.html'
})
export class TabsPage {

  tab1Root = RadioPage;
  tab2Root = ContentPage;
  tab3Root = SchedulePage;
  tab4Root = VideoPage;

  constructor(public http: Http, private restApiProvider: RestApiProvider) {
    this.requestPosts();
    this.requestEvents();
    this.requestNowPlaying();
  }

  requestPosts() {
    this.restApiProvider.requestInfo("posts", "page=1")
      .subscribe(
        posts => {
          this.restApiProvider.posts = posts;
          for (var post of this.restApiProvider.posts) {
            this.requestByline(post.byline[0]);
            this.requestCategory(post.categories[0]);
          }
        },
        error => console.log(error));
  }

  requestByline(id) {
    this.restApiProvider.requestInfo("byline/" + id, "")
      .subscribe(
        byline => this.restApiProvider.bylines[byline["id"]] = byline["name"],
        error => console.log(error));
  }

  requestCategory(id) {
    this.restApiProvider.requestInfo("categories/" + id, "")
      .subscribe(
        category => this.restApiProvider.categories[category["id"]] = category["name"],
        error => console.log(error));
  }

  requestEvents() {
    this.restApiProvider.requestEvent("events", "")
      .subscribe(
        events => {
          this.restApiProvider.events = events["events"];},
        error => console.log(error));
  }

  requestNowPlaying() {
    this.restApiProvider.requestNowPlaying()
      .subscribe(
        nowPlaying => {
          parseString(nowPlaying, function (err, result) {
            this.restApiProvider.nowPlaying = result;
          });
        },
        error => this.restApiProvider.nowPlaying = null);
  }
}



