import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/retry';
import 'rxjs/add/observable/throw';

/*
  Generated class for the RestApiProvider provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular DI.
*/
@Injectable()
export class RestApiProvider {

  private url:string = "https://www.kcpr.org/wp-json/wp/v2/"
  posts: any[] = [];
  bylines: string[] = [];
  categories: string[] = [];
  events: any[] = [];
  schedule: any;
  nowPlaying: any;

  constructor(public http: HttpClient) {
    
  }

  public requestInfo(endpoint, query): Observable<string[]> {
    var searchUrl: string;
    if (query.length == 0) {
      searchUrl = this.url+endpoint;
    }
    else {
      searchUrl = this.url+endpoint+"?"+query;
    }
    console.log(searchUrl);

    return this.http.get(searchUrl)
                    .retry(5)
                    .map(this.extractData)
                    .catch(this.handleError);
  }

  public requestEvent(endpoint, query): Observable<string[]> {
    var searchUrl: string;
    if (query.length == 0) {
      searchUrl = "http://kcpr.org/wp-json/tribe/events/v1/"+endpoint;
    }
    else {
      searchUrl = "http://kcpr.org/wp-json/tribe/events/v1/"+endpoint+"?"+query;
    }
    console.log(searchUrl);

    return this.http.get(searchUrl)
                    .retry(5)
                    .map(this.extractData)
                    .catch(this.handleError);
  }

  public requestNowPlaying(): Observable<string> {
    return this.http.get("http://spinitron.com/radio/rss.php?station=kcpr")
                    .retry(5)
                    .map(this.extractData)
                    .catch(this.handleError);
  }

  
  private extractData(res: Response) {
    let body = res;
    return body || { };
  }

  private handleError (error: Response | any) {
    let errMsg: string;
    if (error instanceof Response) {
      const err = error || '';
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }

  getPosts(): Observable<any[]> {
    return Observable.of(this.posts);
  }

  getBylines(): Observable<any[]> {
    return Observable.of(this.bylines);
  }

  getCategories(): Observable<any[]> {
    return Observable.of(this.categories);
  }

  getEvents(): Observable<any[]> {
    return Observable.of(this.events);
  }

  getSchedule(): Observable<any> {
    return Observable.of(this.schedule);
  }

  getNowPlaying(): Observable<any> {
    return Observable.of(this.nowPlaying);
  }
}



