import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { RestApiProvider } from '../../providers/rest-api/rest-api';
import { EventDetailPage } from '../event-detail/event-detail';
import { Observable } from 'rxjs/Observable';

@Component({
  selector: 'page-schedule',
  templateUrl: 'schedule.html'
})

export class SchedulePage {
  programming: string = "now_playing";
  events: any[] = [];
  schedule: Observable<any[]>;
  nowPlaying: any;

  constructor(public navCtrl: NavController, private restApiProvider: RestApiProvider) {
  }

  ionViewDidEnter() {
    this.restApiProvider.getEvents().subscribe(value => {
      this.events = value;
    })
    this.schedule = this.restApiProvider.requestInfo("pages", "include=4638");
    this.restApiProvider.getNowPlaying().subscribe(value => {
      this.nowPlaying = value;
    })
  }

  itemTapped(event, item) {
    this.navCtrl.push(EventDetailPage, {
      item: item
    });
  }
}



