import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { PostDetailPage } from '../post-detail/post-detail';
import { RestApiProvider } from '../../providers/rest-api/rest-api';

@Component({
  selector: 'page-content',
  templateUrl: 'content.html'
})
export class ContentPage {
  posts: any[] = [];
  bylines: string[] = [];
  categories: string[] = [];

  constructor(public navCtrl: NavController, private restApiProvider: RestApiProvider) {
  }

  ionViewDidEnter() {
    this.restApiProvider.getPosts().subscribe(value => {
      this.posts = value;
    })
    this.restApiProvider.getBylines().subscribe(value => {
      this.bylines = value;
    })
    this.restApiProvider.getCategories().subscribe(value => {
      this.categories = value;
    })
  }

    /*
  doInfinite(infiniteScroll) {
    this.page = this.page+1;
    setTimeout(() => {
      this.restApi.getUsers(this.page)
         .subscribe(
           res => {
             this.data = res;
             this.perPage = this.data.per_page;
             this.totalData = this.data.total;
             this.totalPage = this.data.total_pages;
             for(let i=0; i<this.data.data.length; i++) {
               this.users.push(this.data.data[i]);
             }
           },
           error =>  this.errorMessage = <any>error);

      console.log('Async operation has ended');
      infiniteScroll.complete();
    }, 1000);
  }
     */

  itemTapped(event, item) {
    // go to the post detail page
    // pass session data

    this.navCtrl.push(PostDetailPage, {
      item: item
    });
  }
}



